import React, { memo, useCallback, useEffect, useRef, useState } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux';

// 
import {
  getSongDetailAction, changeSequenceModeAction,
  changeSongAndIndexAction,
  changeLyricIndexAction
} from '../store/actionCreators';
import {
  getSizeImage,
  formatMinuteSecond,
  getPlayUrl
} from '../../../utils/format-utils'

// 
import HYAppPlayPanel from '../app-play-panel'
import { Slider } from 'antd';
import { message } from 'antd'
import { NavLink } from 'react-router-dom';

import {
  AppPlayerWrapper,
  Control,
  PlayInfo,
  Operator
} from './style'
export default memo(function YAppPlayer() {
  // state
  const [currentTime, setCurrentTime] = useState(0);
  const [progress, setProgress] = useState(0);
  const [isChanging, setIsChanging] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [showPanel, setShowPanel] = useState(false);


  // redux
  const { currentSong = {},
    sequenceMode = 0,
    playList = [],
    lyricList = [],
    currentLyricIndex = 0,
  } = useSelector(state => ({
    currentSong: state.getIn(['player', 'currentSong']),
    sequenceMode: state.getIn(['player', 'sequenceMode']),
    playList: state.getIn(['player', 'playList']),
    lyricList: state.getIn(['player', 'lyricList']),
    currentLyricIndex: state.getIn(['player', 'currentLyricIndex']),
  }), shallowEqual);
  const dispatch = useDispatch();

  // hooks
  useEffect(() => {
    // action
    dispatch(getSongDetailAction(1836364963));
    dispatch(getSongDetailAction(458245372));
  }, [dispatch]);
  const audioRef = useRef();

  useEffect(() => {
    audioRef.current.src = getPlayUrl(currentSong.id);
    // isPlaying
    try {
      audioRef.current.play().then(res => {
        setIsPlaying(true);
      }).catch(err => {
        setIsPlaying(false);
      });
    } catch (e) {

    }
  }, [currentSong]);

  const picUrl = (currentSong.al && currentSong.al.picUrl) || '';//
  const singerName = (currentSong.ar && currentSong.ar[0].name) || '';//
  const duration = currentSong.dt || 0;
  const formatDuration = formatMinuteSecond(duration);
  const showCurrentTime = formatMinuteSecond(currentTime);

  // func
  const playMusic = useCallback(() => {
    isPlaying ? audioRef.current.pause() : audioRef.current.play();
    setIsPlaying(!isPlaying);
  }, [isPlaying]);

  // 
  function timeUpdate(e) {
    // console.log('audio-time-update', showCurrentTime);//
    // console.log('timeUpdate:', currentTime);
    let currentTime = e.target.currentTime * 1000;
    if (!isChanging) {
      setCurrentTime(currentTime);//currentTime
      setProgress(currentTime / duration * 100);
    }

    let i = 0;
    for (; i < lyricList.length; i++) {
      let lyricItem = lyricList[i];
      if (currentTime < lyricItem.time) {
        break;
      }
    }
    if (currentLyricIndex !== i - 1) {
      dispatch(changeLyricIndexAction(i - 1));
      // console.log('currentLyric:', lyricList[i - 1]);
      const content = lyricList[i - 1] && lyricList[i - 1].content;
      content && message.open({
        key: 'lyric',
        content,
        duration: 0
      });
    }

  }

  const sliderChange = useCallback((curValue) => {
    // console.log('slide-change:', curValue);
    setIsChanging(true);
    const currentTime = curValue / 100 * duration;
    setCurrentTime(currentTime);
    setProgress(curValue);
  }, [duration]);

  const sliderAfterChange = useCallback((curValue) => {
    // console.log('slider-after', curValue);
    const curTime = curValue / 100 * duration / 1000;
    audioRef.current.currentTime = curTime;
    // console.log('after:', curTime);
    setCurrentTime(curTime * 1000);
    setIsChanging(false);
    if (!isPlaying) {
      playMusic();
    }
  }, [duration, isPlaying, playMusic]);

  // 
  const changeSequenceMode = () => {
    let curSequenceMode = sequenceMode + 1;
    if (curSequenceMode > 2) { // 1 =>2 =>0 =>1 =>...
      curSequenceMode = 0;
    }
    dispatch(changeSequenceModeAction(curSequenceMode));
  }

  const changeMusic = (tag) => {// 1 -1  
    dispatch(changeSongAndIndexAction(tag));
  }

  // 
  const handleMusicEnded = () => {
    if (sequenceMode === 2 || playList.length === 1) {//
      console.log(sequenceMode);
      audioRef.current.currentTime = 0;//
      audioRef.current.play();
    } else {
      dispatch(changeSongAndIndexAction(1));
    }
  }

  return (
    <AppPlayerWrapper className='sprite_playbar'>
      <div className="content wrap-v2">
        <Control isPlaying={isPlaying}>
          <button className='sprite_playbar btn prev'
            onClick={e => { changeMusic(-1) }}></button>
          <button className='sprite_playbar btn play'
            onClick={e => { playMusic() }}></button>
          <button className='sprite_playbar btn next'
            onClick={e => { changeMusic(1) }}></button>
        </Control>
        <PlayInfo>
          <div className="image">
            {/* NavLinklocation */}
            <NavLink to="/discover/player">
              <img src={getSizeImage(picUrl, "35")} alt="" />
            </NavLink>
          </div>
          <div className="info">
            <div className="song">
              <span className="song-name">{currentSong?.name}</span>
              <a href="/#" className="singer-name">{singerName}</a>
            </div>
            <div className="progress">
              <Slider defaultValue={0} value={progress}
                onChange={sliderChange}
                onAfterChange={sliderAfterChange} />
              <div className="time">
                <span className="now-time">{showCurrentTime}</span>
                <span className="divider">/</span>
                <span className="duration">{formatDuration}</span>
              </div>
            </div>
          </div>
        </PlayInfo>
        <Operator sequence={sequenceMode}>
          <div className="left">
            <button className="sprite_playbar btn favor"></button>
            <button className="sprite_playbar btn share"></button>
          </div>
          <div className="right sprite_playbar">
            <button className="sprite_playbar btn volume"></button>
            <button className="sprite_playbar btn loop" onClick={e => { changeSequenceMode() }}></button>
            <button className="sprite_playbar btn playlist"
              onClick={e => setShowPanel(!showPanel)}>{playList.length}</button>
          </div>
        </Operator>
      </div>
      <audio ref={audioRef} onTimeUpdate={e => timeUpdate(e)}
        onEnded={e => handleMusicEnded()} />
      {showPanel && <HYAppPlayPanel />}
    </AppPlayerWrapper>
  )
})
