
// 
import {
  getMusicLyric, getSongDetail, getSimiPlaylist, getSimiSong
} from '../../../services/player';
import {
  parseLyric
} from '@/utils/lrc-parse'
import * as actionTypes from './constants'

const changeCurrentSongAction = (song) => ({
  type: actionTypes.CHANGE_CURRENT_SONG,
  currentSong: song
})

const changePlayListAction = (playList) => ({
  type: actionTypes.CHANGE_PLAY_LIST,
  playList: playList
})

const changeCurrentSongIndexAction = (index) => ({
  type: actionTypes.CHANGE_CURRENT_SONG_INDEX,
  currentSongIndex: index
})

export const changeLyricIndexAction = (lyricIndex) => ({
  type: actionTypes.CHANGE_CURRENT_LYRIC_INDEX,
  currentLyricIndex: lyricIndex
})

export const changeSequenceModeAction = (sequenceMode) => ({
  type: actionTypes.CHANGE_SEQUENCE_MODE,
  sequenceMode: sequenceMode
})

export const changeLyricListAction = (lyricList) => ({
  type: actionTypes.CHANGE_LYRIC_LIST,
  lyricList
})

export const changeSongAndIndexAction = (tag) => {
  return (dispatch, getState) => {
    const playList = getState().getIn(['player', 'playList']);
    const sequenceMode = getState().getIn(['player', 'sequenceMode']);
    let currentSongIndex = getState().getIn(['player', 'currentSongIndex']);

    switch (sequenceMode) {// 0 1 2
      //  
      case 1:
        let randomIndex = Math.floor(Math.random() * playList.length);
        while (randomIndex === currentSongIndex) {//
          randomIndex = Math.floor(Math.random() * playList.length);
        }
        currentSongIndex = randomIndex;
        break;
      default:
        currentSongIndex += tag;
        if (currentSongIndex >= playList.length) currentSongIndex = 0;
        if (currentSongIndex < 0) currentSongIndex = playList.length - 1;
    }
    // action
    const currentSong = playList[currentSongIndex];
    dispatch(changeCurrentSongAction(currentSong));
    dispatch(changeCurrentSongIndexAction(currentSongIndex));
    // 3.
    dispatch(getMusicLyricAction(currentSong.id));
  }
}

const changeSimiPlaylistAction = (res) => ({
  type: actionTypes.CHANGE_SIMI_PLAYLIST,
  simiPlaylist: res.playlists
})

const changeSimiSongsAction = (res) => ({
  type: actionTypes.CHANGE_SIMI_SONGS,
  simiSongs: res.songs
})


// dispatch
export const getSongDetailAction = (ids) => {
  return (dispatch, getState) => {
    // 1.dplayList
    const playList = getState().getIn(['player', 'playList']);
    const songIndex = playList.findIndex(song => song.id === ids)

    // 2.1
    if (songIndex !== -1) {
      const song = playList[songIndex];
      dispatch(changeCurrentSongIndexAction(songIndex));
      dispatch(changeCurrentSongAction(song));
      // 3.
      dispatch(getMusicLyricAction(song.id));
    } else {// 2.2.
      getSongDetail(ids).then(res => {
        const song = res && res.code === 200 && res.songs.length > 0 && res.songs[0];
        if (!song) return;
        // 
        const newPlayList = [...playList];
        newPlayList.push(song);
        // action
        dispatch(changePlayListAction(newPlayList));
        dispatch(changeCurrentSongIndexAction(newPlayList.length - 1));
        dispatch(changeCurrentSongAction(song));
        // 3.
        dispatch(getMusicLyricAction(song.id));

      }).catch(err => {
        console.error('songDetail=>', err);
      });
    }


  }
}

export const getMusicLyricAction = (id) => {
  return dispatch => {
    getMusicLyric(id).then(res => {
      if (res && res.code === 200 && res.lrc && res.lrc.lyric) {
        const lyric = res.lrc.lyric;
        const lyricList = parseLyric(lyric);
        // console.log('lyricList=>', lyricList);
        dispatch(changeLyricListAction(lyricList));
      }
    }).catch(err => {
      console.error('getMusicLyric=>', err);
    });
  }
}

export const getSimiPlaylistAction = () => {
  return (dispatch, getState) => {
    const id = getState().getIn(["player", "currentSong"]).id;
    if (!id) return;

    getSimiPlaylist(id).then(res => {
      dispatch(changeSimiPlaylistAction(res));
    })
  }
}

export const getSimiSongAction = () => {
  return (dispatch, getState) => {
    const id = getState().getIn(["player", "currentSong"]).id;
    if (!id) return;

    getSimiSong(id).then(res => {
      dispatch(changeSimiSongsAction(res));
    })
  }
}