import React, { Fragment, memo } from 'react'

import {
  footerLinks,
  footerImages
} from '@/services/local-data.js'

import {
  FooterWrapper,
  FooterLeft,
  FooterRight,
} from './style'

export default memo(function YAppFooter() {
  return (
    <FooterWrapper>
      <div className="content wrap-v2">

        <FooterLeft>
          <div className="links">
            {
              footerLinks.map((item, index) => {
                return (
                  <Fragment key={item.link}>
                    <a href={item.link} className='link'>{item.title}</a>
                    <span className='line'>|</span>
                  </Fragment>
                )
              })
            }
          </div>

          <div className="copyright">
            <span>1997-2020</span>
            <span>[2018]3506-263</span>
          </div>
          <div className="report">
            <span>0571-89853516</span>
            <span>
              <a href="#/">ncm5990@163.com</a>
            </span>
          </div>
          <div className="info">
            <span>B2-20090191-18</span>
            <span>
              <a href="#/"></a>
            </span>
          </div>
        </FooterLeft>

        <FooterRight>
          {
            footerImages.map(item => {
              return (
                <div className='item' key={item.link}>
                  <a className='icon' href={item.link} target="_blank"></a>
                  <span className='text' ></span>
                </div>
              )
            })
          }
        </FooterRight>
      </div>
    </FooterWrapper>
  )
});
