import { Map } from 'immutable'

import * as actionTypes from './constants'

// store
// MapImmutable
const defaultState = Map({
  topBanners: [],
  hotRecommends: [],
  // 111
  hotRecommends1: [],
  newAlbum: [],
  topUpList: {},
  topNewList: {},
  topOriginList: {},

  // settleSingers
  settleSingers: []
})

function reducer(state = defaultState, action) {
  switch (action.type) {
    case actionTypes.CHANGE_TOP_BANNERS:
      // return { ...state, topBanners: action.topBanners }
      return state.set('topBanners', action.topBanners);//
    case actionTypes.CHANGE_HOT_RECOMMENDS: {
      return state.set('hotRecommends', action.hotRecommends);
    }
    case actionTypes.CHANGE_NEW_ALBUM: {
      return state.set('newAlbum', action.newAlbum);
    }
    // 
    case actionTypes.CHANG_TOP_UP_LIST: {
      return state.set('topUpList', action.topUpList);
    }
    case actionTypes.CHANG_TOP_NEW_LIST: {
      return state.set('topNewList', action.topNewList);
    }
    case actionTypes.CHANG_TOP_ORIGIN_LIST: {
      return state.set('topOriginList', action.topOriginList);
    }
    // 1111
    case actionTypes.CHANGE_HOT_RECOMMENDS1: {
      return state.set('hotRecommends1', action.hotRecommends1);
    }
    case actionTypes.CHANGE_SETTLE_SINGERS: {
      return state.set('settleSingers', action.settleSingers);
    }
    default:
      return state;
  }
}

export default reducer;
