import styled from 'styled-components'

export const FooterWrapper = styled.div`
  width:100%;
  height:172px;
  background:#f2f2f2;
  border-top: 1px solid #d3d3d3;
  .content{
    display: flex;
    justify-content: space-between;
    color: #666;
  }
`;

export const FooterLeft = styled.div`
  margin-top: 15px;
  line-height: 24px;
  .links {
    .link{
      color:#999;
    }
    .line{
      padding: 0 10px;
      color: #999;
    }
    .line:last-child{
      display: none;
    }
  }
  
  .copyright,.report,.info{
    span{
      margin-right: 15px;
    }
  }
`;

export const FooterRight = styled.div`
  display: flex;
  margin-top: 15px;
  .item{
    display: flex;
    flex-direction: column;
    margin-right: 40px;
    a.icon{
      display: inline-block;
      width: 50px;
      height: 45px;
      background-image: url(${require('../../assets/img/sprite_footer_02.png')});
      background-size: 104px 444px;
    }
    span.text{
      display: inline-block;
      width: 52px;
      height: 10px;
      margin-top: 5px;
      background-image: url(${require('../../assets/img/sprite_footer_01.png')});
      background-size: 170px 110px;
    }
  }
  .item:nth-child(1) a.icon {
      background-position: -54px -100px;
  }
  .item:nth-child(2) a.icon {
    background-position: 0 0;
  }
  .item:nth-child(3) a.icon {
    background-position: -54px -50px;
  }
  .item:nth-child(4) a.icon {
    background-position: 0 -100px;
  }

  .item:nth-child(1) span.text{
    background-position: 2px -100px;
  }
  .item:nth-child(2) span.text{
    background-position: 0 0;
  }
  .item:nth-child(3) span.text{
    background-position: 3px -60px;
  }
  .item:nth-child(4) span.text{
    background-position: 0 -80px;
  }
`;