import React, { memo } from 'react'

import classnames from 'classnames'

import {
  HeaderWrapper,
  HeaderLeft,
  HeaderRight
} from './style'
import { NavLink } from 'react-router-dom'
import { Input } from 'antd';
import { SearchOutlined } from '@ant-design/icons';
import { headerLinks } from '../../services/local-data';

export default memo(function YAppHeader() {

  //items jsx
  function showItems() {
    return (
      headerLinks.map((item, index) => {
        if (index < 3) {
          return (
            <NavLink key={item.link} to={item.link} className='nav-item'>{item.title}
              <i className="sprite_01 icon"></i>
            </NavLink>
          )
        } else {
          return (
            <a key={item.link} href={item.link} target='blank'
              className={classnames('nav-item', { 'download': index === 5 })}>{item.title}</a>
          )
        }
      })
    )
  }

  return (
    <HeaderWrapper>
      <div className='content wrap-v1'>
        <HeaderLeft>
          <h1>
            <a href="#" className='logo sprite_01'></a>
          </h1>
          {showItems()}
        </HeaderLeft>

        <HeaderRight>
          <Input className='search' placeholder="///" prefix={<SearchOutlined />} />
          <a href="#/" className='center'></a>
          <a href="#/" className='login'></a>
        </HeaderRight>
      </div>

      <div className='divider'></div>
    </HeaderWrapper>

  )
});
