import originAxios from 'axios'

import {
  TIMEOUT,
  BASE_URL
} from './config'
export default function request(config) {
  return new Promise((resolve, reject) => {
    // 1.
    const instace = originAxios.create({
      baseURL: BASE_URL,
      timeout: TIMEOUT
    });

    // 2.
    instace.interceptors.request.use(config => {
      // console.log('requestsuccess');
      // 1., loading, 

      // 2., token, tokenlogin

      // 3.()
      // config.data = qs.stringify(config.data)
      // console.log(config);

      // 4.
      return config
    }, err => {
      // console.log('requestfailure');
      return err;
    })

    // 3.
    instace.interceptors.response.use(response => {
      // console.log('responsesuccess');
      return response.data;
    }, err => {
      console.error('responsefailure');
      console.error('request.js err==>', err);
      if (err && err.response) {
        switch (err.response.status) {
          case 400:
            err.message = ''
            break
          case 401:
            err.message = ''
            break
          default:
            err.message = ""
        }
      }
      return err
    })

    // 4.
    instace(config).then(res => {
      resolve(res);
    }).catch(err => {
      reject(err);
    })
  });
}
