import React from 'react'

import { Redirect } from 'react-router-dom';
// import YDiscover from React.lazy(() => import('@/pages/discover'));

const YDiscover = React.lazy(() => import('@/pages/discover'));
const YFriend = React.lazy(() => import('@/pages/friend'));
const YMine = React.lazy(() => import('@/pages/mine'));
const YPlayer = React.lazy(() => import('../pages/player'));

const YRecommend = React.lazy(() => import('@/pages/discover/c-pages/recommend'))
const YAlbum = React.lazy(() => import('@/pages/discover/c-pages/album'))
const YRanking = React.lazy(() => import('@/pages/discover/c-pages/ranking'))
const YSongs = React.lazy(() => import('@/pages/discover/c-pages/songs'))
const YDjradio = React.lazy(() => import('@/pages/discover/c-pages/djradio'))
const YArtist = React.lazy(() => import('@/pages/discover/c-pages/artist'))



const routes = [
  {
    path: '/',
    exact: true,
    render: () => {
      return <Redirect to='/discover' />
    }
  },
  {
    path: '/discover',
    component: YDiscover,
    routes: [
      {
        path: '/discover',
        exact: true,
        render: () => {
          return <Redirect to='/discover/recommend' />
        }
      },
      {
        path: '/discover/recommend',
        component: YRecommend
      },
      {
        path: '/discover/ranking',
        component: YRanking
      },
      {
        path: '/discover/songs',
        component: YSongs
      },
      {
        path: '/discover/djradio',
        component: YDjradio
      },

      {
        path: '/discover/artist',
        component: YArtist
      },
      {
        path: '/discover/album',
        component: YAlbum
      },
      {
        path: '/discover/player',
        component: YPlayer
      }
    ]
  },
  {
    path: '/friend',
    component: YFriend
  },
  {
    path: '/mine',
    component: YMine
  },
]

export default routes;