import { Map } from "immutable";

import * as actionTypes from './constants'


const defaultState = Map({
  hotAlbumsList: [],
  topAlbumsList: [],
  albumTotal: 0,
})

export default function reducer(state = defaultState, action) {
  switch (action.type) {
    case actionTypes.CHANGE_HOT_ALBUMS: {
      return state.set('hotAlbumsList', action.hotAlbumsList);
    }
    case actionTypes.CHANGE_TOP_ALBUMS: {
      return state.set('topAlbumsList', action.topAlbumsList);
    }
    case actionTypes.CHANGE_ALBUMS_TOATAL: {
      return state.set('albumTotal', action.albumTotal);
    }
    default:
      return state;
  }
}