import styled from 'styled-components'

export const HeaderWrapper = styled.div`
  background-color:#242424;
  height:75px;
  font-size:14px;
  .content{
    display: flex;
    justify-content: space-between;
    height: 70px;
    line-height:70px;
  }
  .divider{
    height: 5px;
    background-color: #c20c0c;
  }
`
export const HeaderLeft = styled.div`
  display:flex;
  .logo{
    display: inline-block;
    width: 176px;
    height: 70px;
    text-indent: -999px;
  }
  .sprite_01{
    background-position: 0 0;
  }

  .nav-item {
    position: relative;
    display:inline-block;
    height: 70px;
    padding:0 20px;
    color: #ccc;
    text-decoration: none;
    &:hover{
      background-color: #000000;
      color:#fff;
    }
    &.active{
      background-color: #000000;
      color:#fff;
      .sprite_01{
        position: absolute;
        display: inline-block;
        width: 12px;
        height: 7px;
        bottom: -2px;
        left: 50%;
        transform: translateX(-50%);
        background-position: -226px 0;
      }
    }
  }

  .download{
    position:relative;
    &::after{
      content: "";
      position: absolute;
      top: 20px;
      right: -15px;
      width: 28px;
      height: 18px;
      background-image:url(${require('@/assets/img/sprite_01.png')});
      background-position: -190px 0;
    }
  }
`
export const HeaderRight = styled.div`
  display: flex;
  align-items: center;
  color: #ccc;
  font-size: 12px;

  .search {
    width: 158px;
    height: 32px;
    border-radius: 16px;

    input {
      /* input */
      &::placeholder {
        font-size: 12px;
      }
    }
  }

  .center {
    width: 90px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border: 1px solid #666;
    border-radius: 16px;
    margin: 0 16px;
    }
    .center:hover{
      border: 1px solid #fff;
      transition:all 0.3s
    }
    .login:hover,
    .center:hover{
      color: #fff;
      text-decoration: none;
    }
`