import { createStore, applyMiddleware, compose } from 'redux'
import thunk from 'redux-thunk'

import reducer from './reducer'

const storeEnhancer = applyMiddleware(thunk);//saga

// storeEnhancerRedux-devtools
const winCompose = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;
const composeEnhancers = typeof (winCompose) === 'function' && (winCompose({
  trace: true,
}) || compose);

let store = null;
if (typeof (winCompose) === 'undefined') {
  store = createStore(reducer, storeEnhancer);
} else {
  store = createStore(reducer, composeEnhancers(storeEnhancer));
}

export default store;